import * as firebase from 'firebase'
import axios from 'axios'
import db_file from '../db'

export default {
    state: {
        validation_token: 'base64:dXeGr7/AvrT16YlEp35hYlGfv654OGdPvUbPBF7GphM=2018',
        contactRequests: [],
    },
    mutations: {
        setContactRequests (state, value) {
            state.contactRequests = value

        },
        setContactRequest (state, value) {

            state.contactRequests.push(value)

        },
    },
    getters: {
        getContactRequests (state, value)
        {
            return state.contactRequests

        },
    },
    actions: {

        contactRequest({state, commit, dispatch}, value) {

            commit('setLoading',true)
            commit('clearError')

            const db = firebase.firestore()

            // A nova solicitacao de contacto
            db.collection("contactRequests").add(value)
                .then( () => {

                    dispatch('contactRequestMail', value)
                    commit('setContactRequest', value)
                    commit('setLoading', false)

                })
                .catch((error) => {
                    console.log(error)
                    commit('setLoading',false)
                    commit('setError', error)
                })

        },

        contactRequestMail({state, commit}, data) {

            data.validation_token = state.validation_token //colocando o token de validacao

            let urlLocal = 'http://localhost:8000/api/sendContactRequestMail'
            let urlOnline = 'https://explicador-api.herokuapp.com/api/sendContactRequestMail'

            axios.post(urlOnline, data)
                .then((response) =>{

                    commit('setLoading',false)

                })
                .catch((error)=>{
                    commit('setLoading',false)
                    commit('setError', error)
                    console.log(error)
                });



        },

        getAllContactRequests ({commit}) {

            // alert('os contactos foram requisitados')
            const db = firebase.firestore()
            db.collection("contactRequests").get().then(function(querySnapshot) {

                const list = []

                querySnapshot.forEach(function(doc) {

                    const request = doc.data()

                    if (request.created_at)
                    {
                        list.push( request )
                    }



                });

                commit('setContactRequests',list)

            })
                .catch( (error => {
                    console.log('erro buscando a lista dos contactos', error)
                }))


        },

    }

}




