<template>

    <div v-if="todayContacts.length > 0" style="width: 100%">

        <v-flex xs12 sm12>

            <v-card>

                <!--Cabecalho-->
                <v-card-title class="blue darken-1">
                    <span class="headline white--text">Historico de contactos visualizados <v-icon>phone</v-icon></span>
                    <v-spacer></v-spacer>

                    <v-btn flat="" @click.prevent="" v-if="isMyProfile">
                        <!--Aparece aqui a opcao editar...-->
                       <!--Editar <v-icon>edit</v-icon>-->
                    </v-btn>

                    <template v-else="">
                        <v-btn
                                fab=""
                                flat=""
                                small=""
                                outline=""
                                style="float: right;"
                        >
                            <!--<v-icon class="white&#45;&#45;text">more_horiz</v-icon>-->
                        </v-btn>
                    </template>

                </v-card-title>

                <v-list v-if="totalContactsVied">
                    <v-subheader>Contactos de Hoje</v-subheader>

                    <template v-for="contact in todayContacts">

                        <!--Foto de perfil-->
                        <v-list-tile @click="" v-if="isMyProfile">

                            <v-list-tile-action>

                                <profile-photo-upload :user="getUser(contact.explicadorId)" @closing="fechandoDialog" :showDialog="false"></profile-photo-upload>

                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title>{{ contact.explicadorName }}</v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>

                        <!--Email do user-->
                        <v-list-tile @click="">
                            <v-list-tile-action>
                                <v-icon>mail</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title>{{ contact.explicadorEmail }}</v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>

                        <!--Telefone do user-->
                        <v-list-tile @click="">

                            <v-list-tile-action>
                                <v-icon>phone</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title>{{ getUser(contact.explicadorId).telfone }}</v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>

                        <!--<v-flex xs12 sm4>-->
                            <!--<v-btn -->
                                   <!--:to="{ name: 'Profile', params: { userId: contact.explicadorId }}"-->
                                   <!--block="" flat=""-->
                                   <!--class="grey" >-->
                                <!--Ver perfil-->
                            <!--</v-btn>-->
                        <!--</v-flex>-->
                        <v-divider inset class="ma-4"></v-divider>

                    </template>



                </v-list>

            </v-card>

        </v-flex>
    </div>

</template>

<script>
    export default {
        props: ['user', 'name', 'circularSize'],
        data: () => ({
            hidden: false,
            showDialog: true,
            showContactDialogByUserProfile: 0,
            editData: false,
            editGender: '',
            editTelfone: '',
            editName: '',
            toggle: true,
            auth_img1: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTVzJOZxVrO-feT6NaYjyfyhRrtzVyhPVLq39tnTqhG1uXxDuelvg",
            auth_img2: "https://partners.starcertification.org/Content/Partner_Guest/images/graduation_boy-512.png"
        }),
        created () {
            if (this.contactRequests.length === 0 && this.isMyProfile)
            {
                this.$store.dispatch('getAllContactRequests')
            }
        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint'
                }
            },
            users () {
                return this.$store.getters.getUsers
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },

            img () {
                return this.isUser ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.isUser && this.user.name !== null && this.user.name !== '' ? this.user.name : 'Sem Nome'
            },
            gender () {

                const negative = 'Sexo nao definido'

                if (!this.isUser) return negative

                let genero = this.user.gender

                return genero && genero !== null && genero && genero !== '' ? genero : negative


            },
            userInstitution () {

                if (!this.isUser) return 'Sem Universidade (Escola)'

                let inst = this.user.Educations.institution
                return inst !== null && inst !== '' ? inst : 'Sem Universidade (Escola)'
            },
            userFaculty () {

                const negative = 'Sem Faculdade'

                if (!this.isUser) return negative

                let fac = this.user.Educations.faculty
                return this.isUser && fac !== null && fac !== undefined && fac !== '' ? 'Faculdade de ' + fac : negative
            },
            contactRequests () {
                return this.$store.getters.getContactRequests
            },

            totalContactsVied ()
            {
                let countVisualization = 0
                let today = new Date() //data da visualizacao

                this.contactRequests.forEach((r) => {

                    let d1 = new Date(r.created_at.getFullYear(), r.created_at.getMonth(), r.created_at.getDate()); //data da visualizacao
                    let d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                    if (r.userRequesterId === this.userAuth.id && d1.getTime() === d2.getTime())
                    {
                        countVisualization ++
                    }


                })
                return countVisualization
            },

            todayContacts ()
            {
                let todayList = []

                this.contactRequests.forEach((r) => {

                    let d1, d2
                    let today = new Date() //data da visualizacao

                    if (r.created_at) {

                        d1 = new Date(r.created_at.getFullYear(), r.created_at.getMonth(), r.created_at.getDate()); //data da visualizacao
                        d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                        if (r.created_at && r.userRequesterId === this.userAuth.id && d1.getTime() === d2.getTime()) {
                            todayList.push(r)
                        }
                    }

                })
                return todayList
            },
            userCourse () {

                const negative = 'Sem Curso'

                if (!this.isUser) return negative

                let course = this.user.Educations.course

                return course && course !== null && course !== undefined && course !== '' ? 'Curso de ' + course : negative

            },
            userDepartment () {

                const negative = 'Sem Departamento'

                if (!this.isUser) return negative

                let department = this.user.Educations.department

                return department && department !== null && department !== undefined && department !== '' ? department : negative


            },
            userNivel () {

                const negative = 'Sem Nivel'

                if (!this.isUser) return negative

                let nivel = this.user.Educations.nivel

                return nivel && nivel !== null && nivel !== undefined && nivel !== '' ? nivel : negative


            },
            hasSubjects () {

                if ( !this.isUser ) return false

                let subjects = this.user.Educations.subjects

                return subjects.length > 0
            },

            subjectsTitle () {

                if (!this.hasSubjects) return 'Ainda não registou as disciplinas que explica'

                return 'Disciplinas do ' + this.nomeUser
            },
            userSubjects () {

                const negative = []

                if (!this.isUser) return negative

                let subjects = this.user.Educations.subjects

                return subjects


            },

            hasSomeLocation () {

                if ( !this.isUser ) return false

                return this.user.Location

            },

            userProvince () {

                const negative = 'Provincia Não registada'

                if (!this.hasSomeLocation) return negative

                let userProv = this.user.Location.province

                return userProv !== null && userProv !== undefined && userProv !== '' ? userProv : negative

            },

            userCity () {

                const negative = 'Cidade Não registada'

                if (!this.hasSomeLocation) return negative

                let userCity = this.user.Location.city

                return userCity !== null && userCity !== undefined && userCity !== '' ? userCity : negative

            },

            userBairro () {

                const negative = 'Bairro Não registado'

                if (!this.hasSomeLocation) return negative

                let userBairro = this.user.Location.bairro

                return userBairro !== null && userBairro !== undefined && userBairro !== '' ? userBairro : negative

            },

            personalInfoFillPercent ()
            {
                let totalPercent = 0

                if (this.nomeUser !== 'Sem Nome')
                {
                    totalPercent = 20
                }
                if (this.gender !== 'Sexo nao definido')
                {
                    totalPercent += 20
                }
                if (this.userAuth.email !== null && this.userAuth.email)
                {
                    totalPercent += 20
                }
                if (this.userAuth.telfone !== null && this.userAuth.telfone)
                {
                    totalPercent += 20
                }
                if (this.userAuth.photoURL !== this.auth_img1 && this.userAuth.photoURL !== this.auth_img2)
                {
                    totalPercent += 20
                }

                return totalPercent
            }

        },
        watch:  {


        },
        methods: {

            scrollTo (top) {

                window.scroll({
                    top: top,
                    left: 10,
                    behavior: 'smooth'
                });

                // Scroll to a certain element
//                document.querySelector(el).scrollIntoView({
//                    behavior: 'smooth'
//                });

            },

            fechandoDialog () {
                this.showDialog = false
                this.$emit('closing')
            },

            getUser (userId)
            {
                return this.users.find((u) => {
                    return u.id === userId
                })

            }
        },
    }
</script>
<style scoped>
    div.card__title {
        margin-top: 0px;
        padding: 0;
        font-size: 8pt;
    }
    .list__tile__title {
        font-size: 11px!important;
    }
    .headline {
        margin-left: 1em;
    }
    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }


</style>


