<template>

    <div style="width: 100%;">

        <v-flex xs12 sm12>

            <v-card>

                <!--Cabecalho-->
                <v-card-title class="white--text blue">
                    <span v-if="isMyProfile" class="headline">
                        Minha disponibilidade <v-icon dark>event</v-icon>
                    </span>
                    <span v-else="" class="headline">
                        Disponibilidade do explicador {{ user.name }}
                    </span>
                    <v-spacer></v-spacer>
                    <div v-if="isMyProfile" class="white" style="margin-right:100px;border-radius: 100px; padding: 2px">
                        <v-progress-circular
                                :size="circularSize"
                                :width="7"
                                :rotate="360"
                                title="Percentagem de preenchimento"
                                style="float: right;cursor: pointer"
                                :value="personalInfoFillPercent"
                                :color="'blue'"
                                @click="toggle = !toggle"
                        >
                            <span v-if="toggle">{{ personalInfoFillPercent }}</span>
                            <span v-else-if="true" title="Sua disponibilidade!" ><v-icon>done</v-icon></span>
                            <span v-else="" title="Clique em editar, para actualizar a sua disponibilidade">{{ personalInfoFillPercent }}</span>
                        </v-progress-circular>
                    </div>
                    <template v-else="">
                        <v-btn
                                :class="{'red--text' : false }"
                                class="white--text"
                                fab=""
                                flat=""
                                small=""
                                outline=""
                                style="float: right;"
                        >
                            <v-icon class="white--text" :class="{'red--text' : false }">more_horiz</v-icon>
                        </v-btn>
                    </template>
                </v-card-title>

                <!--Corpo-->
                <v-list>

                    <!--Disponibilidade actual-->
                    <template>

                        <v-subheader v-if="isMyProfile" class="hidden-xs-only mb-4">Voçê agora está <b class="ml-2" :class="available ? 'green--text' : 'red--text'">
                            {{ available ? 'Disponível' : 'Indisponível' }}</b>, Clique no lado direito para alterar
                        </v-subheader>
                        <v-subheader v-else="" class="hidden-xs-only mb-4">Esse explicador esta
                            <b class="ml-2 green--text mr-2"> Disponível </b> apenas nos dias onde vem
                            <b class="ml-2" :class="available ? 'green--text' : 'red--text'"> {{ yes }}</b>
                        </v-subheader>

                        <v-layout :class="{'mb-4' : !available }">
                            <v-flex xs12 sm4>
                                <v-chip style="float: left" class="ml-3" :color="available ? 'green' : 'grey'" text-color="white">
                                    <v-avatar>
                                        <v-icon>check_circle</v-icon>
                                    </v-avatar>
                                    {{ available ? 'Disponível' : 'Indisponivel' }}
                                </v-chip>
                            </v-flex>
                            <v-flex xs12 sm4 offset-sm3>
                                <v-switch
                                    :label="available ? yes : no "
                                    :color="available ? 'green' : 'grey'"
                                    :disabled="!isMyProfile"
                                    v-model="available"></v-switch>

                            </v-flex>
                            <v-flex xs12 sm4>
                                <v-btn
                                    v-if="isMyProfile"
                                    title="Gravar todas as alteracoes"
                                    flat=""
                                    :outline="!$vuetify.breakpoint.xsOnly"
                                    color="blue"
                                    round=""
                                    small=""
                                    @click="onSave"
                                    style="float: right"
                                    :disabled="loading && showProcess"
                                    :loading="loading && showProcess">
                                    Gravar
                                    <span slot="loader" class="custom-loader">
                                        <v-icon light>cached</v-icon>
                                    </span>
                                </v-btn>

                            </v-flex>
                        </v-layout>
                        <v-layout v-if="isMyProfile">
                            <small style="float: left" class="ml-3 text-xs-left">
                                NOTA: Se desejar ser visualizado ao listarmos os explicadores, mantenha a sua disponibilidade actual
                                como Sim, se mudar para {{ no }}, ficarás oculto. <span class="text-xs-left red--text">Quando alterar essa informacao nao esquece de gravar</span>
                            </small>
                            <br>

                        </v-layout>
                    </template>

                    <!--Dias de semana-->
                    <template v-if="available">

                        <!--Meio de semana-->
                        <template>
                            <v-subheader class="mt-4">Meio de semana</v-subheader>

                            <!--Segunda-feira-->
                            <v-layout>
                                <v-flex xs12 sm6>
                                    <v-chip style="float: left" class="ml-3" :color="monday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Segunda-feira
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm6>
                                    <v-container fluid>
                                        <v-switch :disabled="!isMyProfile" :label="monday ? yes : no " v-model="monday"></v-switch>
                                    </v-container>
                                </v-flex>
                            </v-layout>

                            <!--Terca-feira-->
                            <v-layout>
                                <v-flex xs12 sm6>
                                    <v-chip style="float: left" class="ml-3" :color="tuesday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Terca-feira
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm6>
                                    <v-container fluid>
                                        <v-switch :disabled="!isMyProfile" :label="tuesday ? yes : no " v-model="tuesday"></v-switch>
                                    </v-container>
                                </v-flex>
                            </v-layout>

                            <!--Quarta-feira-->
                            <v-layout>
                                <v-flex xs12 sm6>
                                    <v-chip style="float: left" class="ml-3" :color="wednesday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Quarta-feira
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm6>
                                    <v-container fluid>
                                        <v-switch :disabled="!isMyProfile" :label="wednesday ? yes : no " v-model="wednesday"></v-switch>
                                    </v-container>
                                </v-flex>
                            </v-layout>

                            <!--Quinta-feira-->
                            <v-layout>
                                <v-flex xs12 sm6>
                                    <v-chip style="float: left" class="ml-3" :color="thursday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Quinta-feira
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm6>
                                    <v-container fluid>
                                        <v-switch :disabled="!isMyProfile" :label="thursday ? yes : no " v-model="thursday"></v-switch>
                                    </v-container>
                                </v-flex>
                            </v-layout>

                            <!--Sexta-feira-->
                            <v-layout>
                                <v-flex xs12 sm6>
                                    <v-chip style="float: left" class="ml-3" :color="friday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Sexta-feira
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm6>
                                    <v-container fluid>
                                        <v-switch :disabled="!isMyProfile" :label="friday ? yes : no " v-model="friday"></v-switch>
                                    </v-container>
                                </v-flex>
                            </v-layout>
                        </template>

                        <!--Fim de semana-->
                        <template>
                            <v-subheader>Fim de semana</v-subheader>

                            <!--Sabado-->
                            <v-layout>
                                <v-flex xs12 sm6>
                                    <v-chip style="float: left" class="ml-3" :color="saturday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Sabado
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm6>
                                    <v-container fluid>
                                        <v-switch :disabled="!isMyProfile" :label="saturday ? yes : no " v-model="saturday"></v-switch>
                                    </v-container>
                                </v-flex>
                            </v-layout>

                            <!--Domingo-->
                            <v-layout>
                                <v-flex xs12 sm4>
                                    <v-chip style="float: left" class="ml-3" :color="sunday ? availableColor : notAvailableColor" text-color="white">
                                        <v-avatar>
                                            <v-icon>check_circle</v-icon>
                                        </v-avatar>
                                        Domingo
                                    </v-chip>
                                </v-flex>
                                <v-flex xs12 sm4 offset-sm3>
                                    <v-container fluid>
                                        <v-switch :disabled="!isMyProfile" :label="sunday ? yes : no " v-model="sunday"></v-switch>
                                    </v-container>
                                </v-flex>
                                <v-flex xs12 sm4>
                                    <v-btn
                                            v-if="isMyProfile"
                                            title="Gravar todas as alteracoes"
                                            flat=""
                                            :outline="!$vuetify.breakpoint.xsOnly"
                                            color="blue"
                                            round=""
                                            small=""
                                            @click="onSave"
                                            style="float: right"
                                            :disabled="loading && showProcess"
                                            :loading="loading && showProcess">
                                        Gravar
                                        <span slot="loader" class="custom-loader">
                                        <v-icon light>cached</v-icon>
                                    </span>
                                    </v-btn>
                                </v-flex>
                            </v-layout>

                            <v-layout class="mb-4">
                                <small style="float: left" class="ml-3 text-xs-left">
                                    NOTA: O facto de marcar sim, como disponivel nesse dia, nao significa que esta disponivel todo dia.
                                </small>
                                <br>

                            </v-layout>
                        </template>

                    </template>

                </v-list>
            </v-card>

        </v-flex>

    </div>

</template>

<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user', 'circularSize', 'showSubjectsEditDialog'],
        data: () => ({
            switch1: false,
            available: false,
            availableColor: 'blue',
            notAvailableColor: 'grey',
            //            no: 'Não',
            no: 'Indisponível',
            yes: 'Sim',
            monday: true,
            tuesday: true,
            wednesday: true,
            thursday: true,
            friday: true,
            saturday: true,
            sunday: true,
            personalInfoFillPercent: 100,
            showProcess: false,
            hidden: false,
            nameU: '',
            toggle: true,
        }),
        mounted () {

            this.inicializeUserInfo()

        },
        watch: {
            loading (value) {
                if (this.showProcess && !value && !this.error) {

                    let nota = 'Veja na página principal, se estás visivel, para ver o efeito das suas alterações '
                    let text = "A informação da sua disponibilidade foi actualizada com sucesso! " + nota
                    let color = 'green'
                    let timeout = 30000
                    eventBus.$emit('showSnackbar', text, color, timeout); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.showProcess = false
                }
            },

        },
        computed: {
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },
            loading () {
                return this.$store.getters.loading
            },



            userBairro () {

                const negative = 'Bairro Não registado'

                if (!this.hasSomeLocation) return negative

                let userBairro = this.user.Location.bairro

                return userBairro !== null && userBairro !== undefined && userBairro !== '' ? userBairro : negative

            },

        },
        methods: {

            onSave ()
            {
                if (!this.isUser || !this.user.Explicador) return alert('voce nao esta auth ou nao eh explicador')//se o user nao estiver autenticado ou nao for explicador nao grava

                this.user.Explicador.availability = {

                    available: this.available,
                    monday: this.monday,
                    tuesday: this.tuesday,
                    wednesday: this.wednesday,
                    thursday: this.thursday,
                    friday: this.friday,
                    saturday: this.saturday,
                    sunday: this.sunday,

                }

                this.showProcess = true
                this.$store.dispatch('updateOnlineUserData', this.user)
            },
            scrollTo (top) {

                window.scroll({
                    top: top,
                    left: 10,
                    behavior: 'smooth'
                });

                // Scroll to a certain element
//                document.querySelector(el).scrollIntoView({
//                    behavior: 'smooth'
//                });

            },

            inicializeUserInfo ()
            {
                this.monday = true
                this.tuesday = true
                this.wednesday = true
                this.thursday = true
                this.friday = true
                this.saturday = true
                this.sunday = true

                if (this.isUser && this.user.Explicador && !this.user.Explicador.hasOwnProperty('availability'))
                {
                    this.available = true
                }
                else if (this.isUser && this.user.Explicador && this.user.Explicador.hasOwnProperty('availability') && this.user.Explicador.availability)
                {
                    let availability = this.user.Explicador.availability

                    this.available = availability.available
                    this.monday = availability.monday
                    this.tuesday = availability.tuesday
                    this.wednesday = availability.wednesday
                    this.thursday = availability.thursday
                    this.friday = availability.friday
                    this.saturday = availability.saturday
                    this.sunday = availability.sunday

                }
                else
                {
                    this.available = false
                }
            }

        },
    }
</script>
<style scoped>
    div.card__title {
        margin-top: 0px;
        padding: 0;
        font-size: 8pt;
    }
    .list__tile__title {
        font-size: 11px!important;
    }
    .headline {
        margin-left: 1em;
    }
    .list__tile__title {
        font-size: 12px!important;
    }
    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }


</style>


