<template>
    <v-dialog v-model="dialog" max-width="500px">

        <!--Botao de activacao fixa da pagina do perfil-->
        <v-btn
            class="mt-5 elevation-0 mr-0"
            :style="$vuetify.breakpoint.mdAndDown ? 'right: 8px;!important;' : 'right: 120px;!important;top: 30px;!important;'"
            :title="this.isUserAuth && !canRequestThisContact ? 'Voce ja atingiu o limite de visualizacoes de contactos' : 'Ver o contacto'"
            v-if="onExplicadorTopProfile && !isMyProfile"
            fab
            top
            right
            :color="hasThisViewedToday ? 'green' : canRequestThisContact ? 'blue' : 'red'"
            dark
            fixed
            small
            slot="activator"
        >
            <v-icon>phone</v-icon>
        </v-btn>
        <v-btn
            v-else=""
            flat=""
            style="margin-top: -5px"
            :icon="!canPersonalize"
            :color="hasThisViewedToday ? 'green' : 'orange'"
            slot="activator"
            @click.stop="dialog = !dialog"
            :round="canPersonalize"
            :block="canPersonalize"
            :outline="canPersonalize"
            >
            {{ canPersonalize ? 'Ver contacto do explicador' : '' }}
            <v-icon class="ml-3" :large="canPersonalize">phone</v-icon>
        </v-btn>


        <v-card>
            <!--Cabecalho da dialog-->
            <v-card-title class="headline pb-1 pt-1">
                Contacto do explicador <v-icon class="ml-3 hidden-xs-only" :medium="$vuetify.breakpoint.mdAndUp">phone</v-icon>

                <v-spacer></v-spacer>
                <v-btn
                   icon=""
                   title="Fechar a Tela"
                   @click.stop="dialog = !dialog">
                    <v-icon>close</v-icon>
                </v-btn>
            </v-card-title>

            <!--Separador-->
            <v-divider></v-divider>

            <v-container grid-list-sm>

                <!--Espaco de erros-->
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <!--Corpo da dialog-->
                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>

                            <!--Termos de uso do contacto do explicador, sao mostrados apenas para os users autenticados.-->
                            <v-flex xs12 v-if="(isUserAuth && canRequestThisContact) || hasThisViewedToday">

                                <small v-if="!isUserAuth && !canShow">
                                    <b class="text-xs-center">
                                        <v-icon style="height: 4px">info</v-icon>
                                        Olha o {{ nomeUser }}
                                        gostaria de saber quem lhe procurou. Por isso vamos pedir o seu contacto primeiro!
                                    <a
                                       href="#/login"
                                       flat="" round=""
                                       title="Fazer Login">
                                        Ou faz login
                                    </a>
                                    </b>
                                </small>
                                <br v-if="!isUserAuth && !canShow">
                                <br v-if="!isUserAuth && !canShow">
                                <b class="text-xs-center">Termos e condições</b>

                                <br>
                                <br>

                                <p>* O contacto é apenas para o uso académico</p>
                                <p>* Não lhe é permitido usar o contacto do <b>{{  nomeUser }}</b> para outros assuntos diferentes de académicos</p>

                                <br>
                            </v-flex>

                            <!--Espaco com o botao de ver o contacto e de login-->
                            <v-flex xs12 v-if="!hasThisViewedToday">

                                <!--Botao solicitar contacto-->
                                <v-layout v-if="(isUserAuth && !canShow) || (isUserAuth && !canRequestThisContact)">
                                    <v-flex xs12>
                                        <v-btn
                                                flat=""
                                                color="blue"
                                                round=""
                                                :disabled="!canRequestThisContact && contactRequests.length > 0"
                                                class=""
                                                :style="$vuetify.breakpoint.smAndDown ? 'height: 30px' : 'height: 35px'"
                                                small=""
                                                block=""
                                                title="Visualizar o contacto desse explicador"
                                                outline=""
                                                @click.stop="onSave()"
                                        >
                                            {{ !$vuetify.breakpoint.mdAndUp ? 'Ver contacto' : 'Ver o contacto do explicador' }}
                                            <v-icon class="ml-3" :small="$vuetify.breakpoint.smAndDown">phone</v-icon>
                                        </v-btn>
                                    </v-flex>
                                </v-layout>

                                <!--Notificacao do limite atingido-->
                                <v-layout v-if="isUserAuth && !canRequestThisContact">
                                    <v-flex xs12>
                                        <small class="red--text">Voçê ja atingiu o seu limite de {{ maxPerDay }} visualizações de contactos por dia, Hoje visualizou {{ totalContactsVied }} contactos</small>
                                    </v-flex>
                                </v-layout>

                                <!--Para o user se autenticar-->
                                <v-layout v-else-if="!isUserAuth">

                                    <v-flex xs12>

                                        <v-layout>
                                            <v-flex v-if="showInfo">
                                                <v-icon title="clique para saber mais"
                                                        style="cursor: pointer"
                                                        class="blue--text"
                                                   @click="showInfo = !showInfo">help</v-icon>
                                                Voçê não esta autenticado, entre ou crie a sua conta para ver os contactos.
                                            </v-flex>
                                            <v-flex v-else=""
                                                title="fechar"
                                                @click="showInfo = !showInfo"
                                                xs12 class="pa-3"
                                                style="border-bottom-right-radius: 20px;border-top-left-radius: 20px; background: #EEE; cursor: pointer">
                                                <p style="font-size: 11pt;line-height: 20px; font-weight: normal">
                                                    O contacto dos explicadores é disponibilizado apenas para os Utilizadores identificados,
                                                    pedimos que faça <b>login</b> ou <b>crie a sua conta</b> para poder ter acesso ao contacto do <b>{{  nomeUser }}</b>.

                                                    <br>
                                                    Por dia voçê só pode ver o contacto de no máximo {{ maxPerDay }} explicadores.
                                                    Por isso te sugerimos que selecione bem os explicadores que desejas contactar.
                                                </p>
                                            </v-flex>
                                        </v-layout>

                                        <v-layout>
                                            <v-flex xs6>

                                                <v-layout>
                                                    <v-flex xs12>
                                                        <v-btn
                                                                router
                                                                :to="{ name: 'Login'}"
                                                                flat=""
                                                                block="">Entrar</v-btn>
                                                        <!--<a flat="" href="#/register" round="" title="Fazer Login">Registar-me</a>-->
                                                    </v-flex>
                                                </v-layout>

                                            </v-flex>
                                            <v-flex xs6>

                                                <v-layout>
                                                    <v-flex xs12>
                                                        <v-btn
                                                            router
                                                            :to="{ name: 'Register'}"
                                                            flat=""
                                                            block="">Registar-me</v-btn>
                                                        <!--<a flat="" href="#/register" round="" title="Fazer Login">Registar-me</a>-->
                                                    </v-flex>
                                                </v-layout>

                                            </v-flex>
                                        </v-layout>

                                    </v-flex>
                                </v-layout>


                            </v-flex>

                        </v-layout>

                    </v-container>

                </v-card-text>


            </v-container>

            <!--Dados de Contacto do explicador mostrados aqui-->
            <v-container v-if="canShow && hasThisViewedToday" style="padding: 0px 10px; font-weight: 100; font-size: 13pt">

                <b class="text-xs-center ml-3">Contactos:</b>

                <br>
                <br>
                <v-layout>
                    <v-flex xs12 class="ml-3">
                        <span class="ma-2">
                            <v-icon>person</v-icon>
                        </span>
                        <span class="ma-2">Nome:</span>
                        <span class="ma-2">{{ nomeUser }}</span>
                    </v-flex>
                </v-layout>
                <v-layout>
                    <v-flex xs12 class="ml-3">
                        <span class="ma-2">
                            <v-icon>mail</v-icon>
                        </span>
                        <span class="ma-2">Email:</span>
                        <span class="ma-2">{{ user.email }}</span>
                    </v-flex>
                </v-layout>
                <v-layout>
                    <v-flex xs12 class="" class="ml-3">
                        <span class="ma-2">
                            <v-icon>phone</v-icon>
                        </span>
                        <span class="ma-2">Telefone:</span>
                        <span class="ma-2">{{ user.telfone ? user.telfone : 'Ainda nao registou numero' }}</span>
                    </v-flex>
                </v-layout>
            </v-container>

            <!--Espacamento quando notifica o limite-->
            <div v-if="!canRequestThisContact && !hasThisViewedToday" style="height: 250px"></div>

            <!--Processamento-->
            <div v-else-if="isUserAuth || !canShow"
                 :style="showInfo && isUserAuth ? 'height: 155px' : showInfo ? 'height: 95px': 'height: 0px'">

                <v-btn
                        @click=""
                        block=""
                        icon=""
                        v-if="showProcess"
                        :disabled="loading"
                        :loading="loading"
                >
                    Gravar

                    <span slot="loader" class="custom-loader">
                        <v-icon light large="">cached</v-icon>
                    </span>
                </v-btn>

            </div>

            <!--Espacamento colocado quando esta autenticado mas ainda nao visualizou.-->
            <div v-if="(isUserAuth || canShow) && !hasThisViewedToday" style="height: 40px"></div>

            <!--Concordancia dos termos-->
            <v-container v-if="!canShow">
                <v-layout>
                    <v-flex class="ml-2">

                        <div class="ml-3">
                            <small>Por dia, voçê so pode visualizar {{ maxPerDay }} contactos</small>
                            <br>
                            <!--<br>-->
                            <small>Ao clicar em ver contacto, voçê  concorda com os Termos</small>
                        </div>
                    </v-flex>
                </v-layout>
            </v-container>

            <!--<div :style="showInfo ? 'height: 40px' : ''"></div>-->
            <!--<div :style="canShow ? 'height: 80px' : ''"></div>-->
            <div v-if="!canRequestThisContact && !hasThisViewedToday" style="height: 50px"></div>

            <v-divider></v-divider>

            <!--Botoes do form-->
            <v-card-actions>
                <v-btn flat color="blue" @click="onHelp">Ajuda</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="onCancel">Fechar</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user', 'canPersonalize', 'onExplicadorTopProfile', 'showContactDialogByUserProfile'],
        created () {

        },
        data () {
            return {

                dialog: false,
                saveOnce: true, //criado so para garantir que grava apenas uma vez.
                canShow: false,
                showProcess: false,
                maxPerDay: 3,
                showInfo: true,
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            userStore () {
                return this.$store.getters.user
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isUserStore () {
                return this.userStore !== null && this.userStore !== undefined
            },
            isMyProfile () {
                return (this.userIsAuth && this.isUser && this.user.id === this.userAuth.id)
                || (this.userIsAuth && this.isUserStore && this.userStore.id === this.userAuth.id)
            },

            nomeUser() {
                return this.isUser && this.user.name && this.user.name !== '' ? this.user.name : 'Ainda nao registou o nome'
            },

            contactRequests () {
                return this.$store.getters.getContactRequests
            },

            totalContactsVied ()
            {
                if (!this.isUserAuth) return 0
                let countVisualization = 0
                let today = new Date() //data da visualizacao

                this.contactRequests.forEach((r) => {

                    let d1 = new Date(r.created_at.getFullYear(), r.created_at.getMonth(), r.created_at.getDate()); //data da visualizacao
                    let d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                    if (r.userRequesterId === this.userAuth.id && d1.getTime() === d2.getTime())
                    {
                        countVisualization ++
                    }


                })
                return countVisualization
            },

            hasThisViewedToday ()
            {
                if (!this.isUserAuth) return false
                let sowed = false

                this.contactRequests.forEach((r) => {

                    let d1, d2
                    let today = new Date() //data da visualizacao

                    if (r.created_at) {

                        d1 = new Date(r.created_at.getFullYear(), r.created_at.getMonth(), r.created_at.getDate()); //data da visualizacao
                        d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                        if (r.created_at && r.userRequesterId === this.userAuth.id && d1.getTime() === d2.getTime() && r.explicadorId === this.user.id) {
                            sowed = true
                            this.canShow = true
                            return
                        }
                    }

                })
                return sowed
            },

            canRequestThisContact ()
            {
                return this.isUserAuth && (this.maxPerDay > this.totalContactsVied) //verifica se o user ainda pode solicitar
            },

        },
        watch: {

            loading (value) {

                if (!value && !this.error && this.showProcess) {

                    this.canShow = true
                    this.showProcess = false

                }

            },
            dialog (value)
            {
                if (value) {

                    if (this.contactRequests.length === 0)
                    {
                        this.$store.dispatch('getAllContactRequests')
                    }

                }
                else //Entao a dialog esta a fechar
                {
                    this.$store.dispatch('setLoading', false)
                }

            },
            showContactDialogByUserProfile (value)
            {
                this.dialog = value > 0 //Se o value for maior que 0 mostre a dialog

            }

        },
        methods: {
            onSave () {

                if (this.canRequestThisContact) //verificar o limite de visualizacoes de contacto hoje.
                {
                    // Gravando os dados que vem do filho:
                    let object = {
                        explicadorId: this.user.id,
                        explicadorName: this.user.name ? this.user.name : '',
                        explicadorEmail: this.user.email,
                        userRequesterId: this.userAuth.id,
                        userRequesterName: this.userAuth.name,
                        userRequesterEmail: this.userAuth.email,
                        userRequesterTelefone: this.userAuth.telefone ? this.userAuth.telefone : '',
                        created_at: new Date(),
                        updated_at: new Date(),
                        permited: true,
                    }

                    this.showProcess = true
                    this.$store.dispatch('contactRequest', object)
                }
            },

            onCancel() {

                this.showProcess = false
                this.$store.dispatch('setLoading', false)
                this.dialog = false

            },

            onDismissed () {
                this.$store.dispatch('clearError')
                this.$store.dispatch('setLoading', false)
            },


            onHelp () {
                this.dialog = false
                this.$router.push('/help')
            }
        }
    }
</script>
<style>
    .dialog:not(.dialog--fullscreen) {
        max-height: 100%;
    }
    .icon {
        font-size: 15px;
    }
</style>


